@import 'fonts.css';

/* Card Component Styles */

.cardComponent_wrapper{
  display: -ms-flexbox;
  display: flex;
}

.slideUp {
    -webkit-animation: slide-up 0.4s ease-out forwards;
            animation: slide-up 0.4s ease-out forwards;
}


.cardComponent{
  height: auto;
  width: 320px;
  margin-bottom: 64px;
  background-color: #FFF;
  border-radius: 8px;
  -webkit-box-shadow: 0px 2px 8px rgb(81,86,229, 0.1);
          box-shadow: 0px 2px 8px rgb(81,86,229, 0.1);
  margin-left: 16px;
  margin-right: 16px;
}

.cardComponent:hover {
  opacity: 1;
  -webkit-box-shadow: 0px 2px 32px rgb(81,86,229, 0.1);
          box-shadow: 0px 2px 32px rgb(81,86,229, 0.1);
  position: relative;
  top: -5px;


}

.cardComponent:hover .CTA-container {
  display: block;
}

.cardComponent:hover .cardComponent_category_wrapper{
  display: block;
}


.cardComponent_img {
  height: 160px;
  width: 100%;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom: 4px solid #F8F8F8;
  margin-bottom: 16px;
}

.cardComponent_name {
  font-family: Montserrat-Medium;
  /* font-weight: 560; */
  font-size: 16px;
  color: #5156E5;
  padding-left: 24px;
  text-decoration: none;
}

.cardComponent_name:hover {
  text-decoration: underline;
}


.cardComponent_description {
  font-family: Montserrat;
  /* font-weight: 400; */
  font-size: 12px;
  color: #818C9F;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 8px;
  line-height: 1.6;
}




/*Card Component CTA Styles */


.CTA-container{
  z-index: 1;
  display: none;
  position: absolute;
  width: 56px;
  height: 24px;
  -webkit-box-shadow: 1px 2px 16px rgb(81,86,229, 0.3);
          box-shadow: 1px 2px 16px rgb(81,86,229, 0.3);
  margin-top : 8px;
  margin-left: 256px;
  border-radius: 2px;
  background-color: #FFF;
  -ms-flex-pack: distribute;
      justify-content: space-around;

}

.CTA-wrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

.CTA-icon{
  width: 16px;
  height: 16px;
  padding-left: 8px;
  padding-top: 4px;
  fill: #5156E5;
  cursor: pointer;
}

.CTA-icon-selected{
  width: 16px;
  height: 16px;
  padding-left: 8px;
  padding-top: 4px;
  fill: #E45050;
  cursor: pointer;
  -webkit-animation: fav 0.4s ease-out forwards;
          animation: fav 0.4s ease-out forwards;
}

/* Notification System */

.notification-system{
  display: block;
}

/*  HELPER CSS */

@-webkit-keyframes slide-up {
    0% {
       -webkit-transform: translateY(8%);
               transform: translateY(8%);
       opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1;
    }

    }

@keyframes slide-up {
    0% {
       -webkit-transform: translateY(8%);
               transform: translateY(8%);
       opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1;
    }

    }




@-webkit-keyframes fav {
      0% {
       -webkit-transform: translateY(8%);
               transform: translateY(8%);
       opacity: 0;
       -webkit-transform: scale(1.0);
       -webkit-transition: scale(1.0);
       -o-transition: scale(1.0);
       transition: scale(1.0);
      }
    50% {
      -webkit-transform: scale(1.2);
      -webkit-transition: scale(1.2);
      -o-transition: scale(1.2);
      transition: scale(1.2);
      -webkit-transform-origin: center;
              transform-origin: center;
    }

    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1;
        -webkit-transform: scale(1.0);
        -webkit-transition: scale(1.0);
        -o-transition: scale(1.0);
        transition: scale(1.0);
      }

      }




@keyframes fav {
      0% {
       -webkit-transform: translateY(8%);
               transform: translateY(8%);
       opacity: 0;
       -webkit-transform: scale(1.0);
       -webkit-transition: scale(1.0);
       -o-transition: scale(1.0);
       transition: scale(1.0);
      }
    50% {
      -webkit-transform: scale(1.2);
      -webkit-transition: scale(1.2);
      -o-transition: scale(1.2);
      transition: scale(1.2);
      -webkit-transform-origin: center;
              transform-origin: center;
    }

    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1;
        -webkit-transform: scale(1.0);
        -webkit-transition: scale(1.0);
        -o-transition: scale(1.0);
        transition: scale(1.0);
      }

      }


/* Media Queries */



@media screen and (min-width : 375px)
and (max-width : 1024px){
  .cardComponent{margin-bottom: 24px;}
  .CTA-container{display:block;}
  .notification-system{display:none;}
}



